/* Scss Document */
@charset "utf-8";
/* CSS Document */

.wrapper_detail {
  .wrap_item_top {
    .thumb_video {
      margin-bottom: 10px;
    }
  }

  .title_video {
    color: #333;
  }

  .social {
    border-top: 1px solid #eee;
  }

  .related {
    background: #f7f7f7;
    padding: 10px;
    margin-bottom: 20px;
    width: 100%;
    float: left;

    li {
      border-bottom: 1px solid #fff;
      font-size: 16px;
      line-height: 1.31;
      margin-bottom: 5px;
      padding-bottom: 5px;

      a {
        color: #004f8c;
      }

      &:last-of-type {
        border: none;
        padding: 0;
        margin: 0;
      }
    }
  }
}

.box_category {
  width: 100%;
  float: left;
  margin-bottom: 20px;
}

.playlist {
  clear: both;
  background: #f7f7f7;
  margin-bottom: 30px;

  .title_playlist {
    width: 100%;
    float: left;
    background: #eee;
    color: #999;
    font-size: 16px;
    line-height: 1.31;
    border: none;
  }

  .ic_playlist_24 {
    margin-right: 10px;
  }

  .list_video_folder {
    padding: 15px;
    overflow-y: scroll;
    max-height: 380px;

    .thumb_video {
      width: 130px;
    }

    .item_video {
      border-bottom: 1px solid #ddd;
      margin-bottom: 10px;
      padding-bottom: 10px;

      &:last-child {
        border: none;
        margin-bottom: 0;
        padding-bottom: 0;
      }
    }
  }

  &.collapse {
    .list_video_folder {
      display: none;
    }
  }
}

.info_video_top {
  padding: 0 15px 0 15px;
}

.social {
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px solid #dddddd;
}

.wrap_social {
  &.right {
    margin-top: -2px;
  }
}

.wrap_count_like {
  width: auto;
  height: 28px;
  line-height: 28px;
  position: relative;
  display: inline-block;
  background: #4080fc;
  color: #fff;
  font-size: 1.3rem;
  text-indent: 25px;
  border-radius: 3px;
  padding: 0 10px 0 0;
}

.wrap_count_like:before {
  content: '';
  width: 5px;
  height: 2px;
  background: #fff;
  position: absolute;
  left: 7px;
  top: 14px;
  transform: rotate(45deg);
}

.fb-like {
  display: inline-block !important;
  float: left !important;
}

.wrap_count_like:after {
  content: '';
  width: 10px;
  height: 2px;
  background: #fff;
  position: absolute;
  left: 9px;
  top: 12px;
  transform: rotate(-45deg);
}


/*COMMENT*/
.txt_blue {
  color: #004f8b !important;
}

.ykien_vne {
  color: #333;
  background: #eaeaea;
  position: relative;
  text-rendering: geometricPrecision;
  padding: 5px 10px;
  font: 400 18px/26px arial;
  color: #9d234c;
}

.ykien_vne .left {
  white-space: nowrap;
}

.filter_coment_mobile {
  float: right;
  position: relative;
  font-size: 14px;
  /*.ic{
    display: inline-block;
    vertical-align: middle;
  }*/
}

.filter_coment_mobile .sub_filter_cmt {
  width: 115px;
  background: #fff;
  position: absolute;
  right: 0;
  top: 30px;
  -webkit-box-shadow: 0px 1px 2px 0px #a3a3a3;
  -moz-box-shadow: 0px 1px 2px 0px #a3a3a3;
  box-shadow: 0px 1px 2px 0px #a3a3a3;
}

.filter_coment_mobile .sub_filter_cmt a {
  display: block;
  padding: 5px 10px;
}

.comment_item {
  padding: 10px;
}

.comment_item.hight_light {
  background: #f8f8f8;
  padding: 10px;
}

.comment_item p {
  padding-bottom: 8px;
  font: 400 16px/21px arial;
  word-wrap: break-word;
}

.comment_item .user_status .left.txt_11 {
  font-size: 14px;
  padding-top: 2px;
}

.comment_item .avata_coment {
  width: 20px;
  margin: 0 10px 0 0;
  float: left;
}

.comment_item .block_like_web {
  width: 100%;
  float: right;
  text-align: right;
  padding: 5px 0 0;
  font-size: 14px;
  position: relative;
  white-space: nowrap;
}

.comment_item .block_like_web a {
  font-size: 14px;
  position: relative;
}

.comment_item .block_like_web a.share_cmt_fb::before {
  width: 1px;
  height: 12px;
  background: #d9d9d9;
  content: "";
  display: inline-block;
  vertical-align: middle;
  margin: 0 10px 0 10px;
}

.comment_item .block_like_web a.share_cmt_fb .ic-caret-down {
  height: 5px;
  background-position: -210px -9px;
  width: 11px;
}

.comment_item .block_like_web .block_share_cmt_fb {
  position: absolute;
  top: 25px;
  right: 0;
  display: none;
  z-index: 3;
  background: #fff;
  padding: 7px;
  -webkit-box-shadow: 0px 1px 2px 0px #a3a3a3;
  -moz-box-shadow: 0px 1px 2px 0px #a3a3a3;
  box-shadow: 0px 1px 2px 0px #a3a3a3;
}

.comment_item .block_like_web .block_share_cmt_fb .item_social {
  float: right;
  margin: 0 0 0 10px;
}

.comment_item .block_like_web .block_share_cmt_fb .item_social:last-child {
  margin: 0;
}

.comment_item .block_like_web .link_thich::after {
  width: 1px;
  height: 12px;
  background: #d9d9d9;
  content: "";
  display: inline-block;
  vertical-align: middle;
  margin: 0 10px 0 10px;
}

.comment_item .block_like_web .total_like {
  padding-left: 2px;
  position: relative;
  /*&::before{width: 2px; height: 2px; background: #666666; position: absolute; left: 2px; top: 10px; content: '';}*/
}

.comment_item .block_like_web .count_like {
  margin-right: 7px;
}

.comment_item a.link_thich {
  position: relative;
  color: #004F8B !important;
  /*.ic{
    color: $vne_color;
    font-size: 15px;
  }*/
}

.comment_item a.link_thich .ic-like,
.comment_item a.link_thich .ic-unlike {
  margin-top: -4px;
}

.comment_item a.link_thich .ic-unlike {
  -webkit-filter: grayscale(100%);
  /* Safari 6.0 - 9.0 */
  filter: grayscale(100%);
}

.comment_item a.link_thich.unlike {
  color: #999 !important;
  /*.ic{
    color: #858585!important;
  }*/
}

.icon_show_full_comment {
  display: inline-block;
  margin: 0 0 0 3px;
  /*.ic{
    color: #999;
  }*/
}

.box_comment_vne .sub_comment {
  padding: 5px 0 0 20px;
}

.box_comment_vne .sub_comment .sub_comment_item {
  padding-top: 10px;
  margin-bottom: 10px;
  border-top: 1px dotted #eee;
}

.box_comment_vne .comment_item .txt_view_more {
  text-align: center;
  margin: 0;
  padding: 0;
  font-size: 14px;
  padding-top: 10px;
  border-top: 1px solid #eee;
}

.box_comment_vne .comment_item .txt_view_more a {
  /*text-decoration: underline;*/
  /*font-weight: 700;*/
}

.box_comment_vne .reply_name {
  color: #004F8B;
  font-style: italic;
}

.box_comment_vne .view_more_coment {
  width: 100%;
  float: left;
  height: 32px;
  text-align: center;
  position: relative;
  margin: 10px 0;
}

.box_comment_vne .view_more_coment a {
  position: relative;
  z-index: 1;
}

.box_comment_vne .view_more_coment a:before {
  height: 1px;
  width: 100%;
  background: #eee;
  position: absolute;
  top: 50%;
  margin-top: -0.5px;
  left: 0;
  content: '';
  z-index: -1;
}

.box_comment_vne .view_more_coment a span {
  background: #f5f5f5;
  padding: 0;
  font: 400 14px/32px arial;
  min-width: 50%;
  color: #004f8c;
  text-align: center;
  display: inline-block;
  position: relative;
  z-index: 1;
}

.input_comment {
  padding: 10px;
  background: #f5f5f5;
  position: relative;
  width: 100%;
  z-index: 2;
}

.input_comment textarea {
  font: 400 16px/18px arial;
  background: #fff;
  border: 1px solid #ccc;
  width: 100%;
  height: 92px;
  overflow: hidden;
  padding: 10px;
}

.input_comment textarea::-webkit-input-placeholder {
  color: #ccc;
}

.input_comment textarea::-moz-input-placeholder {
  color: #ccc;
}

.input_comment textarea::-ms-input-placeholder {
  color: #ccc;
}

.input_comment textarea::-o-input-placeholder {
  color: #ccc;
}

.input_comment .avata_coment {
  width: 30px;
  margin: 0 10px 0 0;
  float: left;
  height: 30px;
}

.input_comment .avata_coment img {
  width: 30px;
  height: 30px;
}

.input_comment span {
  display: inline-block;
  margin-top: 8px;
  color: #666;
  max-width: 120px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.input_comment.reply_input_comment {
  background: none;
  padding: 5px 0;
}

.btn_send_comment {
  float: right;
  margin-top: 7px;
  padding: 7px 15px;
  font-size: 14px;
  font-weight: 400;
}

.btn_send_comment.btn_reset {
  color: #999 !important;
  background: none;
  padding: 7px 10px;
}

.block_relative {
  position: relative;
}

.like_google {
  position: absolute;
  left: 0;
  top: 5px;
  width: auto;
  z-index: 99;
}

.like_google .google_name {
  cursor: pointer;
  font: 700 14px arial;
  min-width: 80px;
  padding: 5px 5px 5px 0;
}

.like_google .not_login {
  font: 400 14px/30px arial;
  color: #666;
}

.like_google .not_login a {
  color: #9F224E;
  text-decoration: underline;
}

.input_comment_full {
  float: none;
  width: auto;
  overflow: hidden;
  padding: 15px;
}

.input_comment_full .btn_send_comment {
  padding: 7px 30px;
}

/*End COMMENT*/

/*COMMENT DARK**/
.box_comment_dark {
  display: none;

  .ykien_vne {
    background: #444;
    color: #999;
  }

  .filter_coment {
    background: #333;
    border-top: 1px solid #666;
    color: #555;
  }

  .comment_item p {
    color: #ccc;
  }

  .box_comment_vne .sub_comment .sub_comment_item {
    border-top: 1px dotted #333;
  }

  .box_comment_vne .comment_item .txt_view_more {
    border-top: 1px solid #333;
  }

  .comment_item.hight_light {
    background: none;
  }

  #box_comment_app_inner .sub_comment .input_comment {
    border-bottom: 1px dotted #333;
  }

  .input_comment textarea {
    background: #333;
    border: 1px solid #333;
    color: #ccc;
  }

  .input_comment {
    background: #222;
  }

  #box_comment_app_inner .view_more_coment a {
    background: #333;
  }

  .box_comment_vne .view_more_coment a:before {
    background: #333;
  }

  #box_comment_app_inner .view_more_coment {
    background: none;
    position: relative;

    &:before {
      width: 100%;
      height: 1px;
      background: #333;
      content: '';
      position: absolute;
      top: 50%;
      margin-top: -0.5px;
      left: 0;
      display: block;
    }
  }

  #box_comment_app_inner a.link_thich,
  .txt_blue,
  #box_comment_app_inner a.link_thich b {
    color: #666 !important;
  }

  .comment_item .block_like_web .link_thich::after {
    background: #666;
  }

  .report_bad:before {
    background: #666;
  }
}